import GameData from "../core/GameData";
import ResManager from "../core/ResManager";
import JSONManager from "../core/JSONManager";
import GameLose from "../view/GameLose";
import GameInfo from "./GameInfo";
import SoundManager from "../core/SoundManager";
import BaseGameStage from "./BaseGameStage";
import TransformTween from "./TransformTween";
import NewPlayerTips from "../view/NewPlayerTips";
import ShiYong from "../view/ShiYong";
import BaseConfig from "../core/BaseConfig";
import GameView from "../view/GameView";

export default class GameStage extends BaseGameStage {
	private static _inst:GameStage;
	static get Inst():GameStage{
		return GameStage._inst;
	}
	camera:Laya.Camera;
	
	men:Laya.Sprite3D;
	player:Laya.Sprite3D;
	playerAni:Laya.Animator;
	playerAniDuration:number;
	road:Laya.Sprite3D;
	playerPos0:Laya.Vector3 = new Laya.Vector3(3, 0, -1.8);
	playerPos1:Laya.Vector3 = new Laya.Vector3(2, 0, -1.8);
	playerPos2:Laya.Vector3 = new Laya.Vector3(0,0,-0.7);
	
	playerPosPhoto:Laya.Vector3 = new Laya.Vector3(0, 0, 0);

    constructor() {
		super();
		GameStage._inst = this;
	}
	onEnable(){
		super.onEnable();

		ResManager.scene3d.input.multiTouchEnabled = false;
		ResManager.scene3d.mouseEnabled = false;
		// ResManager.scene3d.removeChildren();

		this.addChild(ResManager.scene3d);

		this.player = (ResManager.player.getChildByName(""+GameData.Inst.levelItem.model) as Laya.Sprite3D).clone() as Laya.Sprite3D;
		this.playerAni = this.player.getComponent(Laya.Animator) as Laya.Animator;
		this.player.active = false;
		ResManager.scene3d.addChild(this.player);

		this.men = ResManager.scene3d.getChildByName("men") as Laya.Sprite3D;
		this.men.transform.localRotationEulerX=-90;
		this.men.transform.localRotationEulerY=0;
		
		// this.camera = ResManager.scene3d.addChild(ResManager.camera.clone()) as Laya.Camera;
		// this.cameraPos = this.camera.transform.localPosition.clone();
		// this.camera.addComponent(Camera);
		//正交投影属性设置
		// this.camera.orthographic = true;
		//正交垂直矩阵距离,控制3D物体远近与显示大小
		// this.camera.orthographicVerticalSize = 15;
		
		// ResManager.scene3d.addChild(ResManager.light);

/*
		let sceneItem:any = JSONManager.getItemByID(GameData.Inst.levelItem.sceneID, JSONManager.scene);
		this.road0 = ResManager.scene3d.addChild((ResManager.road.getChildByName(sceneItem.map) as Laya.Sprite3D).clone()) as Laya.Sprite3D;
		this.road0.active = true;
		
		//主角出生点
		let tmpSprite3D:Laya.Sprite3D = ResManager.playerPoint.getChildByName(sceneItem.map).getChildByName("player"+sceneItem.born).getChildAt(0) as Laya.Sprite3D;
		this.playerPos = tmpSprite3D.transform.position;
		this.playerPos.y = 0;

		//显示主角
		this.player = ResManager.scene3d.addChild((ResManager.player.getChildAt(0) as Laya.Sprite3D).clone()) as Laya.Sprite3D;
		this.player.active = true;
		*/
	}

	beiJing:Laya.Sprite3D;
	bgMeshRenderer:Laya.MeshRenderer;
	beiJingAni:Laya.Animator;
	showPhoto3D():void{
		this.removeChild(ResManager.scene3d);
		this.addChild(ResManager.scene3dPhoto);

		
		if(GameData.Inst.platform.id == BaseConfig.pid_wx){
			GameView.Inst.ui_jumppage.visible = true;
		}
		
		if(GameData.Inst.data.level<=1){
			NewPlayerTips.Inst.popup();
		}else if(GameData.Inst.data.level>=2){
		}
		GameInfo.Inst.sk_test.play("1", false);
		GameInfo.Inst.list_item.refresh();

		/*
		ResManager.scene3dPhoto.removeChildByName("Main Camera");
		this.camera = ResManager.scene3dPhoto.addChild(ResManager.cameraPhoto.clone()) as Laya.Camera;
		this.camera.fieldOfView = 50;
		*/
		this.camera = ResManager.scene3dPhoto.getChildByName("Main Camera") as Laya.Camera;
		
		ResManager.scene3dPhoto.addChild(this.player);
		this.player.transform.localPosition = this.playerPosPhoto;
		this.player.transform.localRotationEulerY = 0;
		this.playerAni.play("dazhaohu");

		this.beiJing = ResManager.scene3dPhoto.getChildByName("beijing") as Laya.Sprite3D;
		this.bgMeshRenderer = this.beiJing['meshRenderer'];

		this.beiJingAni = this.beiJing.getComponent(Laya.Animator) as Laya.Animator;
		this.beiJingAni.speed = 0.001;
		this.beiJingAni.play("uv_Animation", 0, 0);
		this.beiJingAni.getDefaultState(0).clip.islooping = false;
		this.beiJingAni.getDefaultState(0).clip.duration;
		
		GameInfo.Inst.visible = true;
		GameInfo.Inst.box_step_1.visible = true;
		GameInfo.Inst.box_top.visible = true;
	}
	setBg(skin:string="res/test.jpg"):void{
		GameInfo.Inst.box_step_1.visible = false;
		Laya.Texture2D.load(skin, Laya.Handler.create(this, function(tex){
			//纹理加载完成后赋值
			this.bgMeshRenderer.material['albedoTexture'] = tex;
			this.beiJingAni.speed = 1;

			
			Laya.timer.once(1000, this, ()=>{
				GameInfo.Inst.box_step_2.visible = true;
				this.playerAniDuration = 0;
			});
	  	}));
	}
	setAniRate(rate:number):void{
		if(this.playerAniDuration==0){
			this.playerAni.speed = 0.001;
			this.playerAni.play(GameData.Inst.levelItem.action, 0, 0);
			this.playerAni.getDefaultState(0).clip.islooping = false;
			this.playerAniDuration = this.playerAni.getDefaultState(0).clipEnd;
			
		}
		let playTime:number = this.playerAniDuration*rate;
		this.playerAni.play(GameData.Inst.levelItem.action, 0, playTime);
	}
	
	playerLose():void{
		super.playerLose();

		GameInfo.Inst.off(Laya.Event.END, this, this.timeOver);
		GameInfo.Inst.clearTime();
	}
	playerWin():void{
		this.getCent(GameData.Inst.levelItem.totalReward);
		
		GameInfo.Inst.off(Laya.Event.END, this, this.timeOver);
		GameInfo.Inst.clearTime();
		super.playerWin();
	}

	timeOver():void{
		this.playerLose();
	}
	showGameView():void{
		GameData.Inst.updateLevelJSON();
		//加载游戏主场景
		GameData.Inst.gameScene && Laya.Scene.open(GameData.Inst.gameScene);
	}
	playGame():void{
		super.playGame();
		
		// GameInfo.Inst.on(Laya.Event.END, this, this.timeOver);
		// GameInfo.Inst.timeChange();
		Laya.Tween.to(this.men.transform, {localRotationEulerY:-90}, 300, null, Laya.Handler.create(this, ()=>{
			this.player.active = true;
			this.playerAni.play("zoulu");
			this.playerAni.speed = 1;
			console.log("报错前");
			let tween:TransformTween = this.player.addComponent(TransformTween);
			this.player.transform.localPosition = this.playerPos0;
			tween.rotationY=-45;
			Laya.Tween.to(tween, {x:this.playerPos1.x}, 400);
			Laya.Tween.to(tween, {rotationY:-20}, 200, null, null, 1200);
			Laya.Tween.to(tween, {x:this.playerPos2.x, z:this.playerPos2.z}, 1000, null, Laya.Handler.create(this, ()=>{
				this.playerAni.crossFade("dazhaohu", 0.5);
			}), 400);
			
			Laya.timer.once(3000, this, this.showPhoto3D);
		}));

		
	}

	/**
	 * 重新游戏、开始游戏初始化
	 */
	init():void{
		super.init();

		// this.camera.transform.localPosition = this.cameraPos;
		// this.road0.transform.localPosition = this.road0Pos;
		// this.boss.transform.localPosition = this.road0Pos;
		// this.boss.transform.localPositionY = 1;

		// this.player.transform.localPosition = this.playerPos;
		GameInfo.Inst.visible = false;
		GameStage.Inst.on(Laya.Event.START, this, ()=>{
			console.log("GameStage.Inst.on  Laya.Event.START");
		})
	}
	
	onDisable(){
		super.onDisable();
		if(this.player)this.player.removeSelf();
		this.camera = null;
		this.player = null;
		this.men = null;
		this.playerAni = null;
		this.beiJing = null;
		this.bgMeshRenderer = null;
		this.beiJingAni = null;
	}
	destroy():void{
		super.destroy();
		if(this.player)this.player.removeSelf();
		this.camera = null;
		this.player = null;
		this.men = null;
		this.playerAni = null;
		this.beiJing = null;
		this.bgMeshRenderer = null;
		this.beiJingAni = null;
	}
	
}