import { ui } from "./../ui/layaMaxUI";

import GameData from "../core/GameData";
import GameStage from "./GameStage";
import JSONManager from "../core/JSONManager";
import GameView from "../view/GameView";
import ResManager from "../core/ResManager";
import GameWinVideo from "../view/GameWinVideo";
import SoundManager from "../core/SoundManager";

export default class GameInfo extends ui.view.GameInfoUI {
	private static _inst:GameInfo;
	static get Inst():GameInfo{
		return GameInfo._inst;
	}
    constructor() {
		super();
		GameInfo._inst = this;

	}
	
	onAwake(){
		this.box_step_1.visible = false;
		this.box_step_2.visible = false;
		this.box_step_3.visible = false;
		this.box_top.visible = false;
		this.img_jindu.scrollRect = new Laya.Rectangle(0,0, this.img_jindu.width*0.27, this.img_jindu.height);
		///////////////////1//////////////////
		let skinData:any = [];
		/*for(let i:number=0; i<JSONManager.shop.length; i++){
			skinData.push(JSONManager.shop[i]);
		}*/

		skinData.push(JSONManager.getItemByID(GameData.Inst.levelItem.bgID, JSONManager.shop));
		
		let index0:number = Math.floor(Math.random()*GameData.Inst.data.skin.length);
		if(GameData.Inst.levelItem.bgID==GameData.Inst.data.skin[index0])index0 = (index0+1)%GameData.Inst.data.skin.length;
		let index1:number = (index0+1)%GameData.Inst.data.skin.length;
		if(GameData.Inst.levelItem.bgID==GameData.Inst.data.skin[index1])index1 = (index1+1)%GameData.Inst.data.skin.length;

		skinData.push(JSONManager.getItemByID(GameData.Inst.data.skin[index0], JSONManager.shop));
		skinData.push(JSONManager.getItemByID(GameData.Inst.data.skin[index1], JSONManager.shop));
		
		skinData = skinData.sort((item0, item1)=>{
			return Math.random()>0.45?-1:1;
		});
		this.list_item.array = skinData;

		this.list_item.renderHandler = Laya.Handler.create(this, (cell:ui.items.BgItemUI, index:number)=>{
			let data:any = cell.dataSource;
			
			// console.log("cell index : ", index);data.id==GameData.Inst.data.skinFaxing || 
			if(GameData.Inst.data.skin.indexOf(data.id)!=-1 || data.id==GameData.Inst.trySkinID){//已解锁
				cell.img_show.visible = true;
				cell.img_lock.visible = false;
			}else{
				cell.img_show.visible = true;
				cell.img_lock.visible = true;
			}
			
			cell.img_icon.skin = "shop/"+data.model;
		}, [], false);
		
		this.list_item.on(Laya.Event.CLICK, this, (event:Laya.Event)=>{
			if(event.target.parent instanceof ui.items.BgItemUI){
				let cell:ui.items.BgItemUI = event.target.parent;
				if(cell.img_lock.visible){
					GameData.Inst.platform.videoAD(Laya.Handler.create(this, (id:number)=>{
						GameData.Inst.data.skin.push(id);
						SoundManager.zhengDongLong();
						this.list_item.refresh();
					}, [cell.dataSource.id]), Laya.Handler.create(this, ()=>{
		
					}), 6)

				}else{
					let skin:string = "shop/"+cell.dataSource.model;
					GameStage.Inst.setBg(skin.replace(".png", "_1.jpg"));
					this.img_line.width = 0;
					this.txt_id.text = cell.dataSource.id;

					GameWinVideo.Inst.photoBgID = cell.dataSource.id;
					this.img_jindu.scrollRect = new Laya.Rectangle(0,0, this.img_jindu.width*0.75, this.img_jindu.height);
				}
			}
		});
		///////////////////2//////////////////
		this.btn_line.on(Laya.Event.MOUSE_DOWN, this, ()=>{
			this.btn_line.startDrag(new Laya.Rectangle(0,33,419,1));
			this.stage.on(Laya.Event.MOUSE_UP, this, this.mouseUp);
			Laya.timer.loop(1000/30, this, this.mouseMove)
		});

		///////////////////3//////////////////
		this.btn_photo.on(Laya.Event.MOUSE_DOWN, this, ()=>{
			let w:number = 180*this.viewScaleY;
			let h:number = 300*this.viewScaleY;
			this.btn_photo.startDrag(new Laya.Rectangle(Laya.stage.width/2-w/2,Laya.stage.height/2-h,w,h));
		});
		this.btn_line.on(Laya.Event.MOUSE_UP, this, ()=>{
			this.btn_photo.stopDrag();
		});
		this.btn_ok.on(Laya.Event.CLICK, this, ()=>{
			if(this.photograph)return;
			let xx:number = this.btn_photo.x/this.viewScaleY-(750/this.viewScaleY-750)/2;
			let yy:number = this.btn_photo.y/this.viewScaleY;
			this.txt_xy.text = xx.toFixed(2)+" , "+yy.toFixed(2);
			
			GameWinVideo.Inst.photoX = xx;
			GameWinVideo.Inst.photoY = yy;

			SoundManager.playShutter();


			this.renderTargetCamera = <Laya.Camera>ResManager.scene3dPhoto.addChild(GameStage.Inst.camera.clone());
			this.renderTargetCamera.fieldOfView = 50;
			// renderTargetCamera.transform.position = new Laya.Vector3(-28.8, 8, -60);
			// renderTargetCamera.transform.rotate(new Laya.Vector3(0, 180, 0), true, false);
			//选择渲染目标为纹理
			// this.renderTargetCamera.renderTarget = new Laya.RenderTexture(GameStage.Inst.camera.viewport.width, GameStage.Inst.camera.viewport.height);
			this.renderTargetCamera.renderTarget = new Laya.RenderTexture(Laya.stage.width, Laya.stage.height);
			//渲染顺序
			this.renderTargetCamera.renderingOrder = -1;
			//清除标记
			this.renderTargetCamera.clearFlag = Laya.BaseCamera.CLEARFLAG_SKY;
			let rtex = new Laya.Texture(((<Laya.Texture2D>(this.renderTargetCamera.renderTarget as any))), Laya.Texture.DEF_UV);
			
			let showImg:Laya.Image = GameWinVideo.Inst.img_mine;
			// let showImg:Laya.Image = this.img_photo;
			showImg.texture = rtex;
			// showImg.size(GameStage.Inst.camera.viewport.width, GameStage.Inst.camera.viewport.height);
			showImg.size(Laya.stage.width, Laya.stage.height);
			showImg.scrollRect = new Laya.Rectangle(this.btn_photo.x-348*this.viewScaleY/2, this.btn_photo.y-508*this.viewScaleY/2, 348*this.viewScaleY, 508*this.viewScaleY);
			// this.img_photo.texture = this.img_photo.drawToTexture(this.img_photo.scrollRect.width, this.img_photo.scrollRect.height,0,0) as Laya.Texture;
			
			let scaleRate:number = 227/(348*this.viewScaleY);
			showImg.scale(scaleRate, scaleRate);
			GameWinVideo.Inst.popup(); 
			this.photograph=true;
		});
	}
	renderTargetCamera: Laya.Camera;

	viewScaleX:number=1;
	viewScaleY:number=1;
	mouseUp():void{
		this.stage.off(Laya.Event.MOUSE_UP, this, this.mouseUp);
		Laya.timer.clear(this, this.mouseMove);
		
		this.btn_line.stopDrag();
		// this.mouseMove();
		this.img_jindu.scrollRect = null;

		this.box_step_2.visible = false;
		this.box_step_3.visible = true;

		console.log("GameStage.Inst.camera.viewport" , GameStage.Inst.camera.viewport);
		// this.viewScaleY = GameStage.Inst.camera.viewport.height/1334;
		// this.viewScaleX = GameStage.Inst.camera.viewport.width/750;
		this.viewScaleY = Laya.stage.height/1334;
		this.viewScaleX = Laya.stage.width/750;
		this.btn_photo.scale(this.viewScaleY, this.viewScaleY);


		this.btn_photo.x = Laya.stage.width/2;
		this.btn_photo.y = Laya.stage.height/2;
	}
	mouseMove():void{
		let rate:number = this.btn_line.x/419;
		this.img_line.width = this.btn_line.x;

		this.txt_time.text = ""+rate.toFixed(5);
		GameStage.Inst.setAniRate(rate);
		GameWinVideo.Inst.photoRate = rate;
	}
	photograph:boolean=false;
	onEnable(){
		// this.time = GameData.Inst.levelItem.timeLimit;
		// this.showTime();
		//this.photograph=false;
		this.txt_id.text = "0";
		this.txt_time.text = "0";
		this.txt_xy.text = "0,0";

		this.img_target.skin = "res/level/"+GameData.Inst.levelItem.photo;
		
		Laya.timer.loop(1000/60, this, ()=>{
			if(this.sk_test.templet!=null){
				this.sk_test.x = Laya.stage.width/2;
				this.sk_test.y = Laya.stage.height/2;

				this.sk_test.play("1", false);
				this.sk_test.stop();
				Laya.timer.clearAll(this);
			}
		});
	}

	time:number = 30;
	showTime():void{
		// this.txt_time.text = "00:" + (this.time<10 ? "0"+this.time : this.time);
	}
	timeChange():void{
		this.showTime();
		if(this.time<=0){
			this.event(Laya.Event.END);
			return;
		}
		this.time--;
		Laya.timer.once(1000, this, this.timeChange);
	}
	clearTime():void{
		Laya.timer.clearAll(this);
	}

	
	onDisable(){
		super.onDisable();
		if(this.renderTargetCamera)this.renderTargetCamera.removeSelf();
		this.renderTargetCamera = null;
	}
	destroy():void{
		super.destroy();
		if(this.renderTargetCamera)this.renderTargetCamera.removeSelf();
		this.renderTargetCamera = null;
	}

	
}